"use client";

import { useState } from "react";
import PackageSearch from "../components/PackageSearch";
import PackageCard from "../components/PackageCard";
import { tourPackages, TourPackage } from "../data/tourPackages";

export interface SearchFilters {
  destination?: string;
  departureDate?: string;
  priceRange?: [number, number];
}

export default function PackagesPage() {
  const [filteredPackages, setFilteredPackages] = useState<TourPackage[]>(tourPackages);

  const handleSearch = (filters: SearchFilters) => {
    let filtered = tourPackages;

    if (filters.destination) {
      filtered = filtered.filter(pkg =>
        pkg.destination.toLowerCase().includes(filters.destination.toLowerCase())
      );
    }

    if (filters.departureDate) {
      filtered = filtered.filter(pkg => pkg.departureDate >= filters.departureDate!);
    }

    if (filters.priceRange && filters.priceRange.length === 2) {
      filtered = filtered.filter(
        pkg => pkg.price >= filters.priceRange[0] && pkg.price <= filters.priceRange[1]
      );
    }

    setFilteredPackages(filtered);
  };

  return (
    <main className="min-h-screen bg-gradient-to-b from-white via-sky-50 to-white text-gray-800 px-6 md:px-16 py-16">
      {/* Header */}
      <section className="text-center mb-12">
        <h1 className="text-4xl md:text-5xl font-extrabold text-sky-700 mb-4">
          Explore Our Tour Packages
        </h1>
        <p className="text-lg md:text-xl text-gray-600 max-w-2xl mx-auto">
          Handpicked tours that combine adventure, culture, and relaxation —
          tailored for your dream journey.
        </p>
      </section>

      {/* Search */}
      <div className="max-w-5xl mx-auto mb-12">
        <PackageSearch onSearch={handleSearch} />
      </div>

      {/* Packages Grid */}
      <section className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-10 max-w-7xl mx-auto">
        {filteredPackages.length > 0 ? (
          filteredPackages.map(pkg => <PackageCard key={pkg.id} {...pkg} />)
        ) : (
          <p className="text-center col-span-full text-gray-500 text-lg">
            No packages match your search.
          </p>
        )}
      </section>
    </main>
  );
}
