"use client";
import { useState } from "react";

export interface SearchFilters {
  destination?: string;
  departureDate?: string;
  priceRange?: [number, number];
}

interface PackageSearchProps {
  onSearch: (filters: SearchFilters) => void;
}

export default function PackageSearch({ onSearch }: PackageSearchProps) {
  const [destination, setDestination] = useState("");
  const [price, setPrice] = useState(0);
  const [departureDate, setDepartureDate] = useState("");

  const handleSearch = () => {
    onSearch({
      destination,
      departureDate,
      priceRange: price > 0 ? [0, price] : undefined,
    });
  };

  return (
    <div className="flex flex-col md:flex-row items-center justify-between gap-4">
      <input
        type="text"
        placeholder="Search destination..."
        value={destination}
        onChange={(e) => setDestination(e.target.value)}
        className="w-full md:w-1/3 p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-sky-500"
      />

      <input
        type="date"
        value={departureDate}
        onChange={(e) => setDepartureDate(e.target.value)}
        className="w-full md:w-1/4 p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-sky-500"
      />

      <div className="flex items-center space-x-3 w-full md:w-1/4">
        <input
          type="range"
          min={0}
          max={5000}
          step={50}
          value={price}
          onChange={(e) => setPrice(Number(e.target.value))}
          className="w-full accent-sky-600"
        />
        <span className="text-gray-600 text-sm">
          {price === 0 ? "∞" : `$${price}`}
        </span>
      </div>

      <button
        onClick={handleSearch}
        className="px-6 py-3 bg-sky-600 text-white font-semibold rounded-lg shadow hover:bg-sky-700 transition"
      >
        Search
      </button>
    </div>
  );
}
