import Link from "next/link";
import Image from "next/image";
import { TourPackage } from "../data/tourPackages";

export default function PackageCard({
  id,
  title,
  destination,
  price,
  image,
  description,
  departureDate,
}: TourPackage) {
  return (
    <Link
      href={`/packages/${id}`}
      className="group bg-white rounded-xl shadow-md hover:shadow-xl overflow-hidden transform hover:-translate-y-1 transition-all duration-300"
    >
      <div className="relative h-56 w-full">
        <Image
          src={image}
          alt={title}
          fill
          className="object-cover group-hover:scale-105 transition-transform duration-300"
          sizes="(max-width: 768px) 100vw, (max-width: 1024px) 50vw, 33vw"
        />
        <div className="absolute bottom-0 bg-black/40 text-white px-3 py-1 text-sm rounded-tr-lg">
          {destination}
        </div>
      </div>

      <div className="p-5">
        <h2 className="text-lg font-semibold text-sky-700 group-hover:text-sky-600 transition">
          {title}
        </h2>
        <p className="text-sm text-gray-600 mt-2 line-clamp-2">{description}</p>
        <div className="flex justify-between items-center mt-4">
          <span className="text-sky-700 font-bold">${price.toLocaleString()}</span>
          <span className="text-gray-500 text-sm">{departureDate}</span>
        </div>
      </div>
    </Link>
  );
}
